const getters = {
  ///////Connecté /////////
  getIsConnected: state => state.isConnected,

  //////// profil //////////
  getProfilConnecte: state => state.profilConnecte,

  ///////listePharmacie/////
  getPharmacieListe: state => state.pharmacieListe,

  ///////// Role ///////
  getRole: state => state.role,

    ///////// Localisation///////
  getLocalisationsListe: state => state.localisationsListe,

  getDetailLocalisation: state => state.detailLocalisation,

  ///////// Garde ///////
  getDetailGarde: state => state.detailGarde,

  ///////// Pharmacie ///////
  getDetailPharmacie: state => state.detailPharmacie,

    ///////// Message ///////
    getMessageListe: state => state.MesssageListe
}

const mutations ={

  //////// connecté ///////
  setIsConnected: function(state,isCon){state.isConnected=isCon},

  //////// Profil ///////
  setProfilConnecte: function(state,profilCon){state.profilConnecte=profilCon},
  
  ///////Back//////
  back: function(){history.back();},
  
  ///////// Role ///////
  setRole: function(state,role){ state.role=role},

    ///////// Localisation///////
  addLocalisation: function(state,loca){ state.localisationsListe.push(loca)},
  setDetailLocalisation: function(state,loca){state.detailLocalisation=loca},
  setLocalisationList: function(state,loca){state.localisationsListe=loca},

  ///////// Garde ///////
  setDetailGarde: function(state,gard){state.detailGarde.push(gard)},

  ///////// Pharmacie ///////
  setDetailPharmacie: function(state,pharma){state.detailPharmacie=pharma}
}

export default new Vuex.Store({
  state: state,
  mutations: mutations,
  actions: {
  },
  modules: {
  },
  getters: getters

})