  <div class="home">
    <!-- <HelloWorld msg="HelloWord Page!"/> -->

    <div>
      <div>
        <b-containe fluid>
          <b-tabs
            active-nav-item-class="font-weight-bold text-primary"
            class="nav-justified"
            variant="success"
          >
            <!-- Tab Pharmacies liste -->
            <b-tab title="Pharmacies" active>
              <b-input
                type="text"
                v-model="search"
                placeholder="Recherche une pharmacie"
              ></b-input>
              <div id="scrollPharmacie">
                <div
                  v-for="(pharmacie, index) of filtrePharmacie"
                  v-bind:key="index"
                >
                  <b-button id="ecart2" block variant="info">
                    <b-row>
                      <b-col>
                        <p class="font-weight-bold text-left">
                          {{ pharmacie.nom }}
                        </p>
                        <p>
                          <b-icon icon="geo-alt" aria-hidden="true"></b-icon
                          >{{ pharmacie.lieu }}
                        </p>
                      </b-col>
                      <b-col
                        ><b-badge variant="warning" v-if="pharmacie.ouverte"
                          >Ouverte</b-badge
                        >
                        <b-badge variant="danger" v-else>Fermée</b-badge>
                        <p>
                          <b-icon
                            icon="telephone-fill"
                            aria-hidden="true"
                          ></b-icon>
                          {{ pharmacie.tel }}
                        </p>
                      </b-col>
                    </b-row>
                  </b-button>
                </div>
              </div>
            </b-tab>

            <!-- Tab Messages liste -->
            <b-tab title="Messages">
              <template v-slot:title>
                <span>Messages </span
                ><b-badge pill variant="warning">4</b-badge>
              </template>
              <b-containe fluid v-if="this.$store.getters.getIsConnected">
                <b-container>
                  <b-row align-h="center">
                    <div>
                      <div id="scrollMessage">
                        <div
                          v-for="(message, index) of messages"
                          v-bind:key="index"
                        >
                          <div
                            v-bind:class="
                              message.me ? 'text-right' : 'text-left'
                            "
                          >
                            <b-badge
                              id="badgeMessageRight"
                              variant="warning"
                              v-if="message.me"
                              ><h6 class="text-wrap text-left">
                                {{ message.message }}
                              </h6>
                              <b-badge variant="light" class="text-right">{{
                                message.temp
                              }}</b-badge></b-badge
                            >

                            <b-badge
                              id="badgeMessageLeft"
                              variant="warning"
                              class="text-wrap text-left"
                              v-else
                              ><h6>{{ message.message }}</h6>
                              <b-badge variant="light" class="text-left">{{
                                message.temp
                              }}</b-badge></b-badge
                            >
                          </div>
                        </div>
                      </div>
                    </div>
                  </b-row>
                </b-container>
              </b-containe>
              <b-input-group
                id="sendMessageCart"
                v-if="this.$store.getters.getIsConnected"
              >
                <b-input
                  type="text"
                  v-model="message"
                  placeholder="Taper un message..."
                ></b-input>
                <b-input-group-prepend is-text>
                  <b-icon
                    variant="success"
                    font-scale="1.5"
                    rotate="45"
                    icon="cursor-fill"
                  ></b-icon>
                </b-input-group-prepend>
              </b-input-group>
              <b-row
                id="ecart2"
                align-h="center"
                class="font-weight-bold"
                v-if="!this.$store.getters.getIsConnected"
              >
                <h5>Connctez-vous pour voir les message!</h5>
              </b-row>
            </b-tab>
          </b-tabs>
        </b-containe>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import * as firebase from "firebase/app";

export default {
  name: "Home",
  data() {
    return {
      search: "",
      messages: this.$store.getters.getMessageListe,
    };
  },
  computed: {
    filtrePharmacie: function () {
      return this.listePharmacie.filter((pharmacie) => {
        return pharmacie.nom.toLowerCase().match(this.search.toLowerCase());
      });
    },

    listePharmacie() {
      return this.$store.getters.getPharmacieListe;
    },
  },
  methods: {
    scrollToEnd() {
      var container = document.querySelector("#scroll");
      var scrollHeight = container.scrollHeight;
      container.scrollTop = scrollHeight;
    },
  },
  mounted() {
    this.$store.commit(
      "setLocalisationList",
      firebase.firestore().collection("localisations").get()
    );
    // this.scrollToEnd();
  },
  beforeMount() {
    if (
      this.$store.getters.getIsConnected &&
      this.$store.state.profilConnecte.role == "PHARMACIE"
    ) {
      this.$router.push("/pharmacie-detail");
    }
  },
};
</script>

