import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Hello from '../components/HelloWorld.vue'
import {fauth} from '../firebase'
Vue.use(VueRouter)

  const routes = [
  {
    path: '/hello',
    component: Hello
  },
  {
    path: '/',
    name: 'default',
    component: Home
  },
  {
    path: '/home',
    name: 'Home',
    component: Home,
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
  },
  {
    path: '/localisation-edit',
    name: 'LocalisationEdit',
    component: () => import(/* webpackChunkName: "localisation-edit" */ '../components/LocalisationEdit'),
    meta: {requiresAuth:true}
  },
  {
    path: '/localisation-list',
    name: 'LocalisationList',
    component: () => import(/* webpackChunkName: "localisation-list" */ '../components/LocalisationList'),
    meta: {requiresAuth:true}
  },
  {
    path: '/localisation-detail',
    name: 'LocalisationDetail',
    component: () => import(/* webpackChunkName: "localisation-detail" */ '../components/LocalisationDetail'),
    meta: {requiresAuth:true}
  },
  {
    path: '/garde-detail',
    name: 'GardeDetail',
    component: () => import(/* webpackChunkName: "garde-detail" */ '../components/GardeDetail'),
    meta: {requiresAuth:true}
  },
  {
    path: '/pharmacie-detail',
    name: 'PharmacieDetail',
    component: () => import(/* webpackChunkName: "pharmacie-detail" */ '../components/PharmacieDetail'),
    meta: {requiresAuth:true}
  },
  {
    path: '/connexion-1',
    name: 'Connexion1',
    component: () => import(/* webpackChunkName: "connexion-1" */ '../components/Connexion1'),
  },
  {
    path: '/connexion-2',
    name: 'Connexion2',
    component: () => import(/* webpackChunkName: "connexion-2" */ '../components/Connexion2')
  },
  {
    path: '/connexion-3',
    name: 'Connexion3',
    component: () => import(/* webpackChunkName: "connexion-3" */ '../components/Connexion3')
  },
  // {
  //   path: '/localisation-detail/:id/:pays/:region/:departement/:arrondissement',
  //   name: 'LocalisationDetail',
  //   component: () => import(/* webpackChunkName: "localisation-detail" */ '../components/LocalisationDetail')
  // }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})


/////////Nav Guard///////////
router.beforeEach( (to,from,next)=>{
  //Check for require Auth Guard//
    if(to.matched.some(record => record.meta.requiresAuth)){
        ///check if not logged in
        if(!fauth.currentUser){
          //Go to login
          next({path:"/connexion-1",query:{redirect: to.fullPath}})
        }else{
          //Proceed do Route
          next();
        }
    }else if(to.matched.some(record => record.meta.requiresGuest)){
          ///check if logged in
          if(fauth.currentUser){
            //Go to 
            next({path:"/",query:{redirect: to.fullPath}})
          }else{
            //Proceed do Route
            next();
          }
    }else{
      //Proceed do Route
      next();  
   }

});

export default router
